body {
  background: url('images/tile.jpg');
  font-family: 'Poppins', sans-serif;
  font-weight: 300;
}

.wrapper {
  height: auto;
  background-color: rgba(255,255,255,0.9);
  border-radius: 10px;
  box-shadow: 0px 0px 20px 10px #003411;
  padding-bottom: 15px;
}

#wrapper {
  max-width: 550px;
  margin: 0 auto;
  overflow: hidden;
}

.Navbar {
  width: 100%;
  height: 70px;
  box-shadow: 0px 2.9825618267059326px 7.456404209136963px 0px rgba(0, 0, 0, 0.1);
  display: flex;
}

.Navbar--logo {
    margin: auto;
    position: relative;
    left: -35%;
}

.Hero {
  display: flex;
  flex-direction: column;
  padding-left:  8%;
  padding-right: 8%;
  padding-top: 5%;
}

.Hero--Grid {
    width: 100%;
    height: 100%;
    transform: scale(.9);
    margin-bottom: -20px;
}

.Hero--Heading {
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  font-size: 36px;
  margin-bottom: 0px;
}

.Hero--Paragraph {
  margin-top: 0px;
  font-family: 'Poppins', sans-serif;
  font-weight: 300;
  color: #222222;
  height: 56px;
  width: 320px;
  left: 36px;
  top: 416px;
  border-radius: undefinedpx;
  padding-bottom: 10px;
}

.cards {
  display: flexbox;
  padding-left: 5%;
  width: 600px;
}

.card {
    width: 180px;
    font-size: 12px;
    float: left;
    padding-left: 15px;
    position: relative;
}

.card--image {
    border-radius: 9px;
}

.card--stats {
    display: flex;
    align-items: center;
    margin-bottom: -5px;
}

.card--star {
    height: 14px;
    padding-right: 4px;
}

.card--title {
    margin-bottom: -10px;
    padding-top: 0px;
}

.card--badge {
    position: absolute;
    top: 6px;
    left: 22px;
    background-color: white;
    padding: 5px 7px;
    border-radius: 2px;
    font-weight: bold;
}

.bold {
  font-weight: bold;
}

.gray {
  color: #918E9B;
}

